<template>
  <div class="material">
        <van-nav-bar
            title="选择材质"
            left-text="返回"
            left-arrow
            @click-left="$router.go(-1)"
        />


      <div class="wrap" v-if="choose.id">
          <div class="tag_wrap">
                <div class="scr">
                    <div class="tag">
                        <p> {{choose.name}} </p>
                    </div>
                </div>
                <div class="clear" @click="clear">
                    <p>清除</p>
                    <van-icon name="cross"/>
                </div>
          </div>
      </div>


        <div class="choose_wrap">
            <div>
                <div class="item" v-for="(item, index) in list" :key="item.id" @click="chooseItem(item, index)">
                    <p :class="item.id == choose.id ? 'active' : ''">{{item.name}}</p>
                </div>
            </div>
        </div>

        <div class="btn-wrap">
            <van-button type="primary" size="large" @click="submit" color="#2873ff">确定</van-button>
        </div>
  </div>
</template>
 
<script>
import { mapState, mapGetters } from "vuex";
import api from '../api/index'
export default {
  name: 'material',
  data () {
    return {
        choose: {
            name: '',
            id: ''
        },
        list: [
            // {
            //     name: '不限',
            //     id: '1',
            //     active: false
            // },{
            //     name: '织物',
            //     id: '2',
            //     active: false
            // },{
            //     name: '仿皮',
            //     id: '3',
            //     active: false
            // },{
            //     name: '真皮',
            //     id: '4',
            //     active: false
            // },{
            //     name: 'Alcantara',
            //     id: '5',
            //     active: false
            // }
        ]
    }
  },
  computed: {
      ...mapState({
          s_sex: state => state.s_sex,
          s_material: state => state.s_material
      })
  },
  created() {
        api.tableInfo().then(res => {
            console.log(res.data.data)
            this.list = res.data.data.chairMaterial;
        })
  },
  methods: {
        submit() {
            this.$store.state.s_material = this.choose;
            this.$router.go(-1);
        },
        chooseItem(val, index) {
            this.choose = val;
            this.list.forEach( (item, _index) => {
                item.active = false;
                this.$set(this.list, _index, item);
            })
            val.active = true;
            this.$set(this.list, index, val);
        },
        clear() {
            this.choose = {
                name: '',
                id: ''
            }
        }
  }
}
</script>
 
<style scoped lang ="stylus">
.material
    .wrap
        .tag_wrap
            position relative
            display flex
            background #F8F8F8
            padding 12px
            .tag
                display flex
                align-items center
                background #FFF
                padding 4px
                border-radius 2px
            .clear
                position absolute
                right 12px
                display flex
                align-items center
                top 12px
                padding 4px
                color #2873fe
    .choose_wrap
        padding 8px
        .item 
            display inline-block
            width 25%
            box-sizing border-box
            padding 4px
            p 
                background #F8F8F8
                text-align center
                padding 12px 0
                color #333
                width 100%
            .active
                color #2873fe
                background rgba(40,115,255,0.1)
    .btn-wrap
        position fixed
        left 0
        bottom 0
        width 100%

</style>